<template>
  <div>
    <h1>{{ id ? '编辑' :'新建' }}广告位</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="名称">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="图片">
        <el-button size="small" @click="model.items.push({})"> <i class="el-icon-plus"></i> 添加广告</el-button>

      </el-form-item>
      <el-col :md="12" v-for="(item,i) in model.items" :key="i">
        <el-form-item label="跳转链接">
          <el-input v-model="item.url"></el-input>
        </el-form-item>
        <el-form-item label="图标" style="margin-top:0.5rem">
          <el-upload class="avatar-uploader" :action="$http.defaults.baseURL+'upload'" :show-file-list="false" :on-success="res=>$set(item,'src',res.url)">
            <img v-if="item.src" :src="item.src" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
      </el-col>
      <el-button type="primary" native-type="submit">提交</el-button>
    </el-form>

  </div>
</template>
<script>
export default {
  props: ['id'],
  data() {
    return {
      model: {
        items: [{
          url: '',
          src: ''
        }]
      },
    }
  },
  created() {
    this.id && this.fetch()
  },
  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`/rest/ads/${this.id}`, this.model)
      } else {
        await this.$http.post('/rest/ads', this.model)
      }
      this.$router.push('/ads/list')
      this.$message({
        type: 'success',
        message: '保存成功'
      })
    },
    async fetch() {
      const res = await this.$http.get(`/rest/ads/${this.id}`)
      this.model = Object.assign({}, this.model, res.data)
    }
  },
}
</script>